import React, { useState } from "react";
import {
  Text,
  CardHeader,
  Stack,
  Image,
  Box,
  Heading,
  Card,
  CardBody,
  CardFooter,
  ResponsiveContext,
} from "grommet";
import ProgressiveImage from "react-progressive-image";
import { navigate } from "gatsby";
import Modal from '../../components/Modal'

const Home = ({ dataList }) => {
  const avatarSrc = "";
  const size = React.useContext(ResponsiveContext);
  const [showModal, setShowModal] = useState(false);
  const [activeCardImage, setActiveCardImage] = useState(null)
  const dominantImageColor = "#86356B";
  const placeholder = (
    <div
      style={{
        backgroundColor: dominantImageColor,
        height: 300,
        width: "100%",
      }}
    />
  );

  const widthLookup = {
    xxsmall: "100%",
    xsmall: "50%",
    small: "50%",
    medium: "20%",
    xlarge: "20%",
    full: "20%",
  };
  return (
    <Box
      direction="row"
      wrap={true}
      align="center"
      justify="center"
      alignContent="center"
      margin={{ top: "1%" }}
    >
      {dataList &&
        dataList.map((e, i) => {
          const { thumbnail, title, cta, cardHref } = e;
          const boxProps = cardHref
            ? {
                style: { cursor: "pointer" },
                onClick: () => navigate(cardHref),
              }
            : {};
          return (
            <Box
              {...boxProps}
              outline={false}
              width={widthLookup[size]}
              key={i}
              pad="small"
            >
              <Card elevation="large" width="100%">
                <Stack anchor="bottom-left">
                  {thumbnail && (
                    <CardBody height="small">
                      <ProgressiveImage src={thumbnail} placeholder="">
                        {(src, loading) => {
                          return loading ? (
                            placeholder
                          ) : (
                            <Image
                              onClick={() => {
                                setShowModal(true)
                                setActiveCardImage(src)
                              }} 
                              fit="cover" 
                              src={src} 
                              a11yTitle="bridge" />
                          );
                        }}
                      </ProgressiveImage>
                    </CardBody>
                  )}
                  {avatarSrc && (
                    <CardHeader
                      background="#000000A0"
                      width="small"
                      align="center"
                    >
                      <Box>
                        <Text size="small">$10</Text>
                      </Box>
                    </CardHeader>
                  )}
                </Stack>
                <Box
                  fill={true}
                  justify="center"
                  alignContent="center"
                  align="center"
                >
                  <Heading level="5" textAlign="center">
                    {title}
                  </Heading>
                </Box>
                <CardFooter align="center">
                  <Box
                    fill={true}
                    justify="center"
                    alignContent="center"
                    align="center"
                  >
                    {cta}
                  </Box>
                </CardFooter>
              </Card>
            </Box>
          );
        })}
    {
      showModal && activeCardImage && <Modal onClose={() => setShowModal(false)}>
      <Image fit="cover" src={activeCardImage} a11yTitle="bridge" />
      </Modal>
    }
    </Box>
  );
};

export default Home;
